* {
  margin: 0;
  box-sizing: border-box;
}

body {
  margin: 100px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

input {
  width: 100%;
  height: 40px;
  outline: none;
}

textarea {
  width: 630px;
  height: 339px;
  outline: none;
}

button {
  background: rgb(166, 166, 206);
  display: block;
  /* border: none; */
  padding: 12px 20px;
}

.App {
  line-height: 2.6rem;
}
